*{


  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;
}

input {
  font-size: 1.3rem;
  width: 100%;
  color: grey;
}

.card-grid{
  display:flex;
  flex-direction: row;
  flex-wrap:wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  background-color: rgb(255, 240, 223);
}

.card p{
  padding: 3px;
  text-align: center;
}

.card img{
  max-height: 150px;
}
